/**
 * prism.js tomorrow night eighties for JavaScript, CoffeeScript, CSS and HTML
 * Based on https://github.com/chriskempson/tomorrow-theme
 * @author Rose Pritchard
 */

code[class*="language-"],
pre[class*="language-"] {
	color: #ccc;
	background: none;
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	font-size: 14px;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;

	-moz-tab-size: 4;
	-o-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-moz-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

/* Code blocks */
pre[class*="language-"] {
	padding: 1em;
	overflow: auto;
	margin: 0;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: transparent;
	border-bottom-left-radius: 0.5em;
	border-bottom-right-radius: 0.5em;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: #999;
}

.token.punctuation {
	color: #ccc;
}

.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
	color: #e2777a;
}

.token.function-name {
	color: #6196cc;
}

.token.boolean,
.token.number,
.token.function {
	color: #f08d49;
}

.token.property,
.token.class-name,
.token.constant,
.token.symbol {
	color: #f8c555;
}

.token.selector,
.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
	color: #cc99cd;
}

.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
	color: #7ec699;
}

.token.operator,
.token.entity,
.token.url {
	color: #67cdcc;
}

.token.important,
.token.bold {
	font-weight: bold;
}
.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}

.token.inserted {
	color: green;
}

pre[data-line] {
	position: relative;
	padding: 1em 0 1em 3em;
}

.line-highlight {
	position: absolute;
	left: 0;
	right: 0;
	padding: inherit 0;
	margin-top: 1em; /* Same as .prisms padding-top */

	background: hsla(24, 20%, 50%,.08);
	background: linear-gradient(to right, hsla(24, 20%, 50%,.1) 70%, hsla(24, 20%, 50%,0));

	pointer-events: none;

	line-height: inherit;
	white-space: pre;
}

@media print {
	.line-highlight {
		/*
		 * This will prevent browsers from replacing the background color with white.
		 * It's necessary because the element is layered on top of the displayed code.
		 */
		-webkit-print-color-adjust: exact;
		color-adjust: exact;
	}
}

	.line-highlight:before,
	.line-highlight[data-end]:after {
		content: attr(data-start);
		position: absolute;
		top: .4em;
		left: .6em;
		min-width: 1em;
		padding: 0 .5em;
		background-color: hsla(24, 20%, 50%,.4);
		color: hsl(24, 20%, 95%);
		font: bold 65%/1.5 sans-serif;
		text-align: center;
		vertical-align: .3em;
		border-radius: 999px;
		text-shadow: none;
		box-shadow: 0 1px white;
	}

	.line-highlight[data-end]:after {
		content: attr(data-end);
		top: auto;
		bottom: .4em;
	}

.line-numbers .line-highlight:before,
.line-numbers .line-highlight:after {
	content: none;
}

pre[id].linkable-line-numbers span.line-numbers-rows {
	pointer-events: all;
}
pre[id].linkable-line-numbers span.line-numbers-rows > span:before {
	cursor: pointer;
}
pre[id].linkable-line-numbers span.line-numbers-rows > span:hover:before {
	background-color: rgba(128, 128, 128, .2);
}

pre[class*="language-"].line-numbers {
	position: relative;
	padding-left: 3.8em;
	counter-reset: linenumber;
}

pre[class*="language-"].line-numbers > code {
	position: relative;
	white-space: inherit;
}

.line-numbers .line-numbers-rows {
	position: absolute;
	pointer-events: none;
	top: 0;
	font-size: 100%;
	left: -3.8em;
	width: 3em; /* works for line-numbers below 1000 lines */
	letter-spacing: -1px;
	border-right: 1px solid #999;

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

}

	.line-numbers-rows > span {
		display: block;
		counter-increment: linenumber;
	}

		.line-numbers-rows > span:before {
			content: counter(linenumber);
			color: #999;
			display: block;
			padding-right: 0.8em;
			text-align: right;
		}

.command-line-prompt {
	border-right: 1px solid #999;
	display: block;
	float: left;
	font-size: 100%;
	letter-spacing: -1px;
	margin-right: 1em;
	pointer-events: none;
	text-align: right;

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.command-line-prompt > span:before {
	opacity: 0.7;
	content: ' ';
	display: block;
	padding-right: 0.8em;
}

.command-line-prompt > span[data-user]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] $";
}

.command-line-prompt > span[data-user="root"]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] #";
}

.command-line-prompt > span[data-prompt]:before {
	content: attr(data-prompt);
}

.command-line-prompt > span[data-continuation-prompt]:before {
	content: attr(data-continuation-prompt);
}

.command-line span.token.output {
	/* Make shell output lines a bit lighter to distinguish them from shell commands */
	opacity: 0.7;
}

div.code-toolbar {
	border: 1px solid #686870;
	border-radius: 0.5em;
}

div.code-toolbar-header {
	display: flex;
	align-items: center;
	width: 100%;
	height: 40px;
	border-bottom: 1px solid #27272a;
}

div.code-toolbar-os {
	display: flex;
	height: 100%;
	align-items: center;
	padding: 0 1.25rem;
	gap: 0.5rem;
}
div.code-toolbar-os > div {
	width: 12px;
	height: 12px;
	border-radius: 100%;
	background: #444;
}
div.code-toolbar:hover {
	div.code-toolbar-os > div:nth-child(1) {
		background: #ED6A5E;
	}
	div.code-toolbar-os > div:nth-child(2) {
		background: #F5BE4F;
	}
	div.code-toolbar-os > div:nth-child(3) {
		background: #60C353;
	}
}

div.code-toolbar-title {
	display: flex;
	width: 100%;
	justify-content: flex-end;
	padding: 0 1rem;
}

div.code-toolbar .toolbar {
	transition: opacity 0.3s ease-in-out;
	opacity: 0;
}

div.code-toolbar:hover .toolbar {
	opacity: 1;
}

/* Separate line b/c rules are thrown out if selector is invalid.
   IE11 and old Edge versions don't support :focus-within. */
div.code-toolbar:focus-within .toolbar {
	opacity: 1;
}

div.code-toolbar .toolbar > .toolbar-item {
	display: inline-block;
}

div.code-toolbar .toolbar > .toolbar-item > a {
	cursor: pointer;
}

div.code-toolbar .toolbar > .toolbar-item > button {
	background: none;
	border: 0;
	color: inherit;
	font: inherit;
	line-height: normal;
	overflow: visible;
	padding: 0;
	-webkit-user-select: none; /* for button */
	-moz-user-select: none;
	-ms-user-select: none;
}

div.code-toolbar .toolbar > .toolbar-item > a,
div.code-toolbar .toolbar > .toolbar-item > button,
div.code-toolbar .toolbar > .toolbar-item > span {
	color: var(--primary-text-color);
	font-size: .8em;
	padding: 0.25em .5em;
	background: #f5f2f0;
	background: rgba(224, 224, 224, 0.2);
	border-radius: .25em;
}

div.code-toolbar .toolbar > .toolbar-item > a:hover,
div.code-toolbar .toolbar > .toolbar-item > a:focus,
div.code-toolbar .toolbar > .toolbar-item > button:hover,
div.code-toolbar .toolbar > .toolbar-item > button:focus,
div.code-toolbar .toolbar > .toolbar-item > span:hover,
div.code-toolbar .toolbar > .toolbar-item > span:focus {
	color: var(--secondary-text-color);
	text-decoration: none;
}

pre.diff-highlight > code .token.deleted:not(.prefix),
pre > code.diff-highlight .token.deleted:not(.prefix) {
	background-color: rgba(255, 0, 0, .1);
	color: inherit;
	display: block;
}

pre.diff-highlight > code .token.inserted:not(.prefix),
pre > code.diff-highlight .token.inserted:not(.prefix) {
	background-color: rgba(0, 255, 128, .1);
	color: inherit;
	display: block;
}

.token.treeview-part .entry-line {
	position: relative;
	text-indent: -99em;
	display: inline-block;
	vertical-align: top;
	width: 1.2em;
}
.token.treeview-part .entry-line:before,
.token.treeview-part .line-h:after {
	content: "";
	position: absolute;
	top: 0;
	left: 50%;
	width: 50%;
	height: 100%;
}
.token.treeview-part .line-h:before,
.token.treeview-part .line-v:before {
	border-left: 1px solid #ccc;
}
.token.treeview-part .line-v-last:before {
	height: 50%;
	border-left: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
.token.treeview-part .line-h:after {
	height: 50%;
	border-bottom: 1px solid #ccc;
}
.token.treeview-part .entry-name {
	position: relative;
	display: inline-block;
	vertical-align: top;
}
.token.treeview-part .entry-name.dotfile {
	opacity: 0.5;
}

/* @GENERATED-FONT */
@font-face {
	font-family: "PrismTreeview";
	/**
	 * This font is generated from the .svg files in the `icons` folder. See the `treeviewIconFont` function in
	 * `gulpfile.js/index.js` for more information.
	 *
	 * Use the following escape sequences to refer to a specific icon:
	 *
	 * - \ea01 file
	 * - \ea02 folder
	 * - \ea03 image
	 * - \ea04 audio
	 * - \ea05 video
	 * - \ea06 text
	 * - \ea07 code
	 * - \ea08 archive
	 * - \ea09 pdf
	 * - \ea0a excel
	 * - \ea0b powerpoint
	 * - \ea0c word
	 */
	src: url("data:application/font-woff;base64,")
		format("woff");
}

