.nav,
.nav a {
  color: var(--primary-text-color);
}

.navigation {
  display: flex;
  margin-top: 64px;
}

.navigation-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
}

.navigation-item:hover {
  opacity: 1;
}

.navigation-previous {
  margin-right: 16px;
}

.navigation-next {
  align-items: flex-end;
  margin-left: auto;
  text-align: right;
}

.navigation-label {
  position: relative;
  z-index: 2;
  font-size: 11px;
  font-weight: 700;
  color: var(--secondary-text-color);
  text-transform: uppercase;
}

.navigation-title {
  position: relative;
  z-index: 2;
  margin: 8px 0 0;
  font-size: 2rem;
  line-height: 1.4;
  word-break: break-word;
  color: var(--primary-text-color);
}

.navigation-item:hover .navigation-title {
  opacity: 0.8;
}

@media (max-width: 767px) {
  .navigation {
    display: block;
  }

  .navigation-item {
    width: 100%;
  }

  .navigation-next {
    margin-top: 8px;
  }
}
