:root {
  --primary-color: var(--ghost-accent-color, #d9ff00);
  --primary-text-color: #f5f5f5;
  --primary-background-color: #0d1017;
  --secondary-text-color: #c9c7c7;
  --white-color: #181A25;
  --light-gray-color: #4b4b4b;
  --mid-gray-color: #858585;
  --dark-gray-color: #e7e5e5;
  --black-color: #ffffff;
  --font-sans: "Open Sans", -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif;
  --font-serif: Lora, Times, serif;
  --animation-base: ease-in-out;
  --shadow-base: 0 0 10px rgba(0, 0, 0, 0.02);
  --content-font-size: 1.7rem;

  /** Sub-headings */
  --color-darker-gray: #ddd;
  /** inline code */
  --color-lighter-gray: #181818;
}

body {
  background-color: var(--primary-background-color);
}
